<template>
  <div>
    <el-row>
      <div style="margin-left:22%">
          <el-row>
          <el-col :span="12">
            <el-input v-model="title" placeholder="标题" type="textarea"></el-input>
          </el-col>
          </el-row>
        <br/>
          <el-row>
            <el-col :span="5">
              <img :src="'http://www.nxin.com.cn'+titleimg" alt="封面图" style="height:200px;width:200px">
            </el-col>
            <el-col :span="8">
              <a class="upload">
                上传封面图
                <input type="file" value="添加封面图" accept="image/*" name="upfile" @change="onUpload"></input>
              </a>
              </el-col>
          </el-row>
        <br/>
        <br/>
        <br/>
        <el-row style="width:1024px;height:800px;">
          <iframe src="../../static/utf8-php/index.html" frameborder="no" style="height:100%;width:100%" id="iframeUeditor" name="iframeUeditor"></iframe>
        </el-row>
      </div>
    </el-row>
    <br/>

    <el-row>
      <el-button type="success" @click="getContent()">提交</el-button>
    </el-row>
  </div>
</template>
<script>




export default {
  data() {
    return {
      editor: null,
      title: '',
      titleimg:''
    }
  },
  mounted() {
    
  },
  methods: {
    onUpload(e){
        let file = e.target.files[0];
        let param = new FormData(); //创建form对象
        param.append('upfile', file);//通过append向form对象添加数据
        let config = {
          headers: { 'Content-Type': 'multipart/form-data' }
        };  //添加请求头
        this.$axios.post('http://www.nxin.com.cn/static/utf8-php/php/controller.php?action=uploadimage', param, config)
          .then(response => {
            this.titleimg=response.data.url
          })
    },
    getContent() {
      let content = document.getElementById('iframeUeditor').contentWindow.getContent()
      if (this.title.length > 1 && content.length > 1) {
        this.$confirm('确认体提交, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          const loading = this.$loading({
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });
          this.$axios.post('apiproxy.php', JSON.stringify({
            type: 'nxforum/article/add/v1',
            data: {
              author_id: this.userinfo.usr_guid,
              title: this.title,
              content: content,
              titleimg:this.titleimg,
              tab: 'good'
            }
          }))
            .then(result => {
              loading.close()
              this.$router.push({
                name: 'articlelist',
              })
            })
            .catch(e => {
              console.log(e)
              loading.close()
            })
        })
      }
    }
  },
  computed: {
    userinfo: function() {
      return JSON.parse(decodeURIComponent(this.getCookie('userInfo')))
    }
  }
}
</script>

<style>

.upload {
  padding: 4px 10px;
  height: 20px;
  line-height: 20px;
  position: relative;
  cursor: pointer;
  color: #888;
  background: #fafafa;
  border: 1px solid #ddd;
  border-radius: 4px;
  overflow: hidden;
  display: inline-block;
  *display: inline;
  *zoom: 1
}

.upload input {
  position: absolute;
  font-size: 100px;
  right: 0;
  top: 0;
  opacity: 0;
  filter: alpha(opacity=0);
  cursor: pointer
}

.upload:hover {
  color: #444;
  background: #eee;
  border-color: #ccc;
  text-decoration: none
}

</style>



