

	export const setCookie=function (name, value, days) {
	    var d = new Date;
		d.setTime(d.getTime() + 24*60*60*1000*days);
	    window.document.cookie = name + "=" + value + ";path=/;expires=" + d.toGMTString();
	};

	export const getCookie=function (name) {
		var result;
		var v = window.document.cookie.match('(^|;) ?' + name + '=([^;]*)(;|$)');
		return result = v ? v[2] : null;
	};

	export const deleteCookie=function (name) {
	    window.document.cookie = name + "='';path=/;expires=-1";
	};




	export default {
		install(Vue,options){
			Vue.prototype.setCookie = function (name, value, minutes) {
			    var d = new Date;
			    d.setTime(d.getTime() + 60*60*1000*minutes);
			    window.document.cookie = name + "=" + value + ";path=/;expires=" + d.toGMTString();
			};
			Vue.prototype.getCookie = function (name) {
			    var v = window.document.cookie.match('(^|;) ?' + name + '=([^;]*)(;|$)');
			    return v ? v[2] : null;
			};
			Vue.prototype.deleteCookie = function (name) {
			    window.document.cookie = name + "='';path=/;expires=-1";
			}
		}
	}


