<template>
    <div>
        <el-row>
            <el-row>
                <el-col :span="8">
                    <div>
                        <el-radio-group v-model="radioQT" @change="handleRadio">
                            <el-radio-button label="全部"></el-radio-button>
                            <el-radio-button label="未审核"></el-radio-button>
                            <el-radio-button label="老赵说菜"></el-radio-button>
                            <!-- <el-radio-button label="热门贴"></el-radio-button> -->
                        </el-radio-group>
                    </div>
                </el-col>
                <el-col :span="8" class="col-search">
                    <el-input placeholder="支持*模糊查询" v-model="inputSearch" @keydown.enter.native="handleSearchClick">
                        <el-select v-model="searchSelect" slot="prepend" placeholder="请选择" style="width:105px">
                            <el-option label="标题" value="title"></el-option>
                            <el-option label="用户名" value="nickname"></el-option>
                        </el-select>
                        <el-button slot="append" icon="el-icon-search" @click="handleSearchClick" size="small"></el-button>
                    </el-input>
                </el-col>
            </el-row>

        </el-row>
        <br/>
        <el-row>

            <el-table :data="articleList" border v-loading="Loading" size="small" :max-height="(clientHeight-190)">
                <!-- <el-table-column align="center" type="selection" width="55"></el-table-column> -->
                <el-table-column label="id" prop="id" align="left" width="50"></el-table-column>
                <el-table-column label="创建人" prop="author.nickname" align="left" width="200"></el-table-column>
                <el-table-column label="创建时间" prop="create_at" align="left" width="200"></el-table-column>
                <el-table-column label="标题" prop="title" align="left"></el-table-column>
                <el-table-column label="状态" prop="approve" align="left" filter-placement="bottom-end" width="150">
                    <template slot-scope="scope">
                        <el-tag type="primary" :type="scope.row.approve==0?'primaty':(scope.row.approve==1?'success':'danger')">{{ scope.row.approve==0?'未审核':(scope.row.approve==1?'审核已通过':'审核未通过') }}
                        </el-tag>
                    </template>
                </el-table-column>
                <el-table-column fixed="right" label="操作" width="100">
                    <template slot-scope="scope">

                        <el-button type="text" size="small" @click="handleViewDetail(scope.row)">查看</el-button>
                        <el-button type="text" size="small" @click="handleDelete(scope.row)">删除</el-button>

                    </template>
                </el-table-column>
            </el-table>
            <div class="block" align="right">
                <el-pagination @current-change="handleCurrentChange" :current-page.sync="pageno" layout="total, prev, pager, next, jumper">
                </el-pagination>
            </div>
        </el-row>

        <el-dialog title="帖子详情" :visible.sync="dialogVisible" :close-on-click-modal="false" :modal-append-to-body="false" v-loading="Loading" @close="handeDialogClose">
            <div v-html="Content"></div>
            <hr></hr>

            <el-collapse v-model="activeName" class="elcollapse" v-if="dialogVisible">
                <el-collapse-item title="查看图片" name="1" align="left">
                    <el-card>
                        <el-row :gutter="10">
                            <el-col :span="8" v-for="(img,i) in imgList" :key="i">
                                <img :src="img" alt="" style="width:100%;height:100%">
                            </el-col>
                        </el-row>
                    </el-card>
                </el-collapse-item>
                <el-collapse-item title="查看评论" name="2" align="left">
                    <el-card>
                        <el-tree :data="dataReply" :props="replyProps" node-key="id" default-expand-all :expand-on-click-node="false" :render-content="renderContent">
                        </el-tree>
                    </el-card>
                </el-collapse-item>
            </el-collapse>
            <br/>
            <el-button type="primary" size="small" @click="handleApprove(1)">审核通过</el-button>
            <el-button type="warning" size="small" @click="handleApprove(2)">审核不通过</el-button>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            articleList: [],
            officer: false,
            pageno: 1,
            dialogVisible: false,
            Content: '',
            imgList: [],
            Loading: false,
            selectRow: '',
            radioQT: '全部',
            searchSelect: 'title',
            inputSearch: '',
            clientHeight: 0,
            activeName: '',
            dataReply: [],
            replyProps: {
                label: 'content',
                children: 'comments'
            }
        }
    },
    mounted() {
        this.initData(this.pageno)
    },
    beforeCreate() {
        var ua = window.navigator.userAgent.toLowerCase();
        if (ua.match(/MicroMessenger/i) == 'micromessenger') {
            this.$router.push({ name: 'wearticles' })
        }
    },
    created() {
        this.clientHeight = document.documentElement.clientHeight
        window.onresize = (e) => {
            this.clientHeight = document.documentElement.clientHeight
        }
    },
    methods: {
        initData(_pageno) {
            this.Loading = true
            let _subdata = {
                officer: this.officer,
                manage: true,
                pageno: _pageno,
                pagesize: 16,
                approve: -1,
                usr_id: this.userinfo.usr_guid
            }
            this.getArticles(_subdata)
        },
        handleSearchClick() {
            this.pageno = 1
            if (this.inputSearch.length > 0) {
                let _data = {
                    officer: this.officer,
                    manage: true,
                    pageno: this.pageno,
                    pagesize: 16,
                    approve: this.radioQT == '未审核' ? 0 : -1,
                    usr_id: this.userinfo.usr_guid
                }

                _data[this.searchSelect] = this.inputSearch

                this.getArticles(_data)
            }
            else {
                this.initData(1)
            }
        },
        getArticles(subdata) {
            this.$axios.post('apiproxy.php', JSON.stringify({
                type: 'nxforum/article/list/v1',
                data: subdata
            }))
                .then(result => {
                    this.articleList = result.data.data
                    this.Loading = false
                })
                .catch(e => {
                    console.error(e)
                    this.Loading = false
                })
        },
        getMyArticles(_pageno) {
            this.Loading = true
            this.$axios.post('apiproxy.php', JSON.stringify({
                type: 'nxforum/myarticle/list/v1',
                data: { usr_id: this.userinfo.usr_guid, pageno: _pageno, pagesize: 16 }
            }))
                .then(result => {
                    this.articleList = result.data.data
                    this.Loading = false
                })
                .catch(e => {
                    console.error(e)
                    this.Loading = false
                })
        },
        handeDialogClose(){
            this.dialogVisible=false
        },
        onLoadReply(val) {
            if (val == 2) {
                this.$axios.post('apiproxy.php', JSON.stringify({
                    type: 'nxforum/manage/reply/list/v1',
                    data: {
                        usr_guid: this.userinfo.usr_guid,
                        article_id: this.selectRow.id
                    }
                })).then(res => {
                    this.dataReply = res.data.data
                }).catch(e => {
                    console.error(e)
                })
            }
        },
        renderContent(h, { node, data, store }) {
            return (
                <span style="flex: 1; flex; white-space: initial;align-items: center; justify-content: space-between; font-size: 14px; padding-right: 8px;">
                    <span>
                        {node.label}
                    </span>
                    <span>
                        <el-button style="font-size: 12px;" type="text" on-click={() => this.removeReply(node, data)}></el-button>
                    </span>
                </span>);
        },
        removeReply(node, data) {
            // console.log(node)  TODO
            console.log(data)
        },
        handleCurrentChange() {
            let _subdata = {
                officer: this.officer,
                manage: true,
                pageno: this.pageno,
                pagesize: 16,
                approve: -1,
                usr_id: this.userinfo.usr_guid
            }
            if (this.inputSearch.length > 0) {
                _subdata[this.searchSelect] = this.inputSearch
            }

            if (this.radioQT == '未审核') {
                _subdata.approve = 0
            } else if (this.radioQT == '老赵说菜') {
                return this.getMyArticles(this.pageno)
            }
            this.getArticles(_subdata)
        },
        handleRadio() {
            this.pageno = 1
            let _data = {
                officer: this.officer,
                manage: true,
                pageno: 1,
                pagesize: 16,
                approve: -1,
                usr_id: this.userinfo.usr_guid
            }

            switch (this.radioQT) {
                case '全部':
                    this.initData(1)
                    break;
                case '未审核':
                    _data.approve = 0
                    this.getArticles(_data)
                    break;
                case '老赵说菜':
                    this.getMyArticles(this.pageno)
                    break;
                case '热门贴':

                    break;
                default:
                    break;
            }
        },
        handleViewDetail(row) {
            this.dataReply = []
            this.$axios.post('apiproxy.php', JSON.stringify({
                type: 'nxforum/article/list/v1',
                data: {
                    id: row.id,
                    usr_id: ''
                }
            }))
                .then(result => {
                    this.dialogVisible = true
                    this.selectRow = row
                    this.Content = result.data.data[0].content
                    this.imgList = result.data.data[0].titleimg ? result.data.data[0].titleimg.split(',') : []
                    this.onLoadReply(2)
                })
                .catch(e => {
                    console.error(e)

                })
        },
        handleDelete(row) {
            this.$confirm('确认删除选中的项目, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.Loading = true
                this.$axios.post('apiproxy.php', JSON.stringify({
                    type: 'nxforum/article/del/v1',
                    data: {
                        article_id: row.id,
                        usr_id: ''
                    }
                }))
                    .then(result => {
                        this.pageno = 1
                        this.initData(this.pageno)
                        this.$message({
                            type: 'success',
                            message: '删除成功'
                        });
                    })
                    .catch(e => {
                        console.log(e)
                        this.$message({
                            type: 'error',
                            message: e
                        });
                    })
            })
        },
        handleApprove(_approve) {
            this.Loading = true

            this.$axios.post('apiproxy.php', JSON.stringify({
                type: 'nxforum/article/approve/v1',
                data: {
                    article_id: this.selectRow.id,
                    usr_id: this.userinfo.usr_guid,
                    approve: _approve
                }
            }))
                .then(result => {
                    this.Loading = false
                    this.dialogVisible = false
                    this.articleList.forEach(item => {
                        if (item.id == this.selectRow.id) {
                            item.approve = _approve
                        }
                    })
                    this.$message({
                        type: 'success',
                        message: '审核设置成功'
                    });
                })
                .catch(e => {
                    console.log(e)
                    this.Loading = false
                    this.$message({
                        type: 'error',
                        message: e
                    });
                })
        }
    },
    computed: {
        userinfo: function() {
            return JSON.parse(decodeURIComponent(this.getCookie('userInfo')))
        }
    }
}
</script>

<style>

.elcollapse .el-tree-node__content {
    height: 45px !important;
}
</style>



