<template>
    <div>
        <el-container>
            <el-header>
                <el-row>
                    <el-tabs v-model="approve" @tab-click="handleClick">
                        <el-tab-pane label="全部" name="-1"></el-tab-pane>
                        <el-tab-pane label="未审核" name="0"></el-tab-pane>
                        <el-tab-pane label="老赵说菜" name="5"></el-tab-pane>
                    </el-tabs>
                </el-row>
                <el-row>
                    <el-col>
                        <el-input placeholder="" v-model="inputSearch" size="small" @keydown.enter.native="handleSearchClick">
                            <el-select v-model="searchSelect" slot="prepend" placeholder="请选择" style="width:90px">
                                <el-option label="标题" value="title"></el-option>
                                <el-option label="用户名" value="nickname"></el-option>
                            </el-select>
                            <el-button slot="append" icon="el-icon-search" @click="handleSearchClick" size="small"></el-button>
                        </el-input>
                    </el-col>
                </el-row>
            </el-header>
            <br/>

            <br/>
            <div class="feed-box" v-loading="Loading">
                <div v-for="(i,lindex) in articleList" class="feed-li" :key="lindex">
                    <div @click="handleDetail(i)">
                        <div class="feed-title">
                            <div class="feed-label" :class="[i.approve==2 ? 'feed-label-unapprove' : `feed-label-other`]">
                                {{i.approve | translateApprove}}
                            </div>&nbsp;
                            <div class="feed-label" :class="[i.top ? 'feed-label-top' : `feed-label-other`]">
                                {{i.tab | translateTab(i.top)}}
                            </div>
                            <p v-text="i.title"></p>
                        </div>
                        <div class="feed-content">
                            <div class="avatar">
                                <img :src="i.author.headimgurl" alt="headImgUrl">
                            </div>
                            <div class="feed-right">
                                <div class="feed-right-top">
                                    <div class="feed-name" v-text="i.author.nickname">
                                    </div>
                                    <div class="feed-count">
                                        <span v-text="i.visit_count"></span>次浏览
                                    </div>
                                </div>

                                <div class="feed-right-bottom" v-if="i.titleimg">
                                    <div class="" style="text-align:left">
                                        <img style="height:80px;width:30%;margin-right:2px;" :src="im" v-for="(im,imindex) in i.titleimg.split(',')" :key="imindex" alt="">
                                    </div>

                                </div>

                                <div class="feed-right-bottom">
                                    <div class="feed-time">
                                        创建于：
                                        <span>{{i.create_at | formatDate('yyyy-MM-dd hh:mm:ss')}}</span>
                                    </div>
                                    <div class="feed-pass">
                                        {{i.last_reply_at | timeAgo}}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <el-dialog title="帖子详情" :visible.sync="dialogVisible" :fullscreen="true" :close-on-click-modal="false" :modal-append-to-body="false" v-loading="Loading">
                <el-row style="position:positive;bottom:10px;">
                    <el-col :span="8">
                        <el-button type="primary" size="small" @click="handleApprove(1)">审核通过</el-button>
                    </el-col>
                    <el-col :span="8">
                        <el-button type="warning" size="small" @click="handleApprove(2)">审核不通过</el-button>
                    </el-col>
                    <el-col :span="8">
                        <el-button type="danger" size="small" @click="handleDelete">删帖</el-button>
                    </el-col>
                </el-row>
                <br/>
                <br/>
                <div v-html="Content"></div>
                <hr/>
                
    <el-row v-if="selectRow.titleimg">
        <el-col :span="8" v-for="(o, index) in selectRow.titleimg.split(',')" :key="index">
            <el-card :body-style="{ padding: '1px' }">
                <img :src="o" class="image" style="width:100%;height:150px;">
            </el-card>
        </el-col>
    </el-row>


            </el-dialog>
        </el-container>
    </div>
</template>
<script>

export default {
    data() {
        return {
            approve: '-1',
            inputSearch: '',
            Loading: false,
            articleList: [],
            officer: false,
            dialogVisible: false,
            Content: '',
            selectRow: '',
            searchSelect: 'title'
        }
    },

    mounted() {
        this.initData()
    },
    methods: {
        initData() {
            this.Loading = true
            let _subdata = {
                officer: this.officer,
                manage: true,
                pageno: 1,
                pagesize: 16,
                approve: -1,
                usr_id: this.userinfo.usr_guid,
            }
            this.getArticles(_subdata)
        },
        handleClick() {
            let _subdata
            if (this.approve == 5) {
                this.getMyArticles()
            } else {
                this.officer = false
                _subdata = {
                    officer: this.officer,
                    manage: true,
                    pageno: 1,
                    pagesize: 100,
                    approve: this.approve,
                    usr_id: this.userinfo.usr_guid
                }
                this.getArticles(_subdata)
            }

        },
        handleDetail(item) {
            this.getDetail(item)
            this.dialogVisible = true
        },
        handleSearchClick() {
            if (this.inputSearch.length > 0) {
                if (this.approve == 5) {
                    this.getMyArticles()
                }
                else {
                    let _data = {
                        officer: this.officer,
                        manage: true,
                        pageno: 1,
                        pagesize: 100,
                        approve: this.approve,
                        usr_id: this.userinfo.usr_guid
                    }

                    _data[this.searchSelect] = this.inputSearch

                    this.getArticles(_data)
                }

            }
        },
        getArticles(subdata) {
            this.Loading = true
            this.$axios.post('apiproxy.php', JSON.stringify({
                type: 'nxforum/article/list/v1',
                data: subdata
            }))
                .then(result => {
                    this.articleList = result.data.data
                    this.Loading = false
                })
                .catch(e => {
                    console.error(e)
                    this.Loading = false
                })
        },
        getMyArticles() {
            this.Loading = true
            this.$axios.post('apiproxy.php', JSON.stringify({
                type: 'nxforum/myarticle/list/v1',
                data: { usr_id: this.userinfo.usr_guid, pageno: 1, pagesize: 100 }
            }))
                .then(result => {
                    this.articleList = result.data.data
                    this.Loading = false
                })
                .catch(e => {
                    console.error(e)
                    this.Loading = false
                })
        },
        getDetail(row) {
            this.$axios.post('apiproxy.php', JSON.stringify({
                type: 'nxforum/article/list/v1',
                data: {
                    id: row.id,
                    usr_id: this.userinfo.usr_guid
                }
            }))
                .then(result => {
                    this.dialogVisible = true
                    this.selectRow = row
                    this.Content = result.data.data[0].content
                    console.log(this.selectRow)
                })
                .catch(e => {
                    console.log(e)

                })
        },
        handleDelete() {
            this.$confirm('确认删除选中的项目, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.Loading = true
                this.$axios.post('apiproxy.php', JSON.stringify({
                    type: 'nxforum/article/del/v1',
                    data: {
                        article_id: this.selectRow.id,
                        usr_id: ''
                    }
                }))
                    .then(result => {
                        this.dialogVisible = false
                        this.initData()
                        this.$message({
                            type: 'success',
                            message: '删除成功'
                        });
                    })
                    .catch(e => {
                        console.log(e)
                        this.$message({
                            type: 'error',
                            message: e
                        });
                    })
            })
        },
        handleApprove(_approve) {
            this.Loading = true

            this.$axios.post('apiproxy.php', JSON.stringify({
                type: 'nxforum/article/approve/v1',
                data: {
                    article_id: this.selectRow.id,
                    usr_id: this.userinfo.usr_guid,
                    approve: _approve
                }
            }))
                .then(result => {
                    this.Loading = false
                    this.dialogVisible = false
                    this.articleList.forEach(item => {
                        if (item.id == this.selectRow.id) {
                            item.approve = _approve
                        }
                    })
                    this.$message({
                        type: 'success',
                        message: '审核设置成功'
                    });
                })
                .catch(e => {
                    console.log(e)
                    this.Loading = false
                    this.$message({
                        type: 'error',
                        message: e
                    });
                })
        }
    },
    computed: {
        userinfo: function() {
            // return {
            //     usr_guid: "f500c4d965ff4b40826063b2e897870e"
            // }
            return JSON.parse(decodeURIComponent(this.getCookie('userInfo')))
        }
    },
}
</script>

<style lang="less">
@globalButtonColor: #80bd01;

.feed-box {
    background-color: #f0f0f0;
}










/** feed **/

.avatar {

    width: 20px;
    height: 20px;
    img {

        width: 20px;
        height: 20px;
    }
}

.feed-title {
    display: flex;
    align-items: center;
    min-width: 0;
}

a {
    color: #333;
}




.feed-li {
    list-style: none;
    padding: 4px;
    position: relative;
    background-color: white;
    margin-bottom: 2px;
}



.feed-title p {
    margin: 0 0 0 8px;
    font-size: 14px;
    line-height: 24px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    flex: 1;
    min-width: 0;
    text-align: left;
}

.feed-content {
    display: flex;
    font-size: 13px;
    margin-top: 8px;
}

.feed-time {
    font-size: 12px;
    color: #666;
}

.feed-right {
    flex: 1;
    min-width: 0;
    margin-left: 8px;
}

.feed-right .feed-right-top,
.feed-right .feed-right-bottom {
    display: flex;
    justify-content: space-between;
}

.feed-right-bottom {
    margin-top: 4px;
}



.feed-label {
    font-size: 12px;
    border-radius: 4px;
    padding: 0 5px;
    height: 20px;
    text-align: center;
    line-height: 20px;
    flex-shrink: 0;
    box-sizing: border-box;
}

.feed-label-top {
    color: #fff;
}

.feed-label-unapprove {
    background-color: red;
    color: #fff;
}

.feed-label-other {
    background-color: #e3e3e3;
    color: #333;
}
</style>




