<template>
  <div>
    <el-container>
      <el-header>
        <el-row>
          <el-col :span="20" style="background-color:#545c64;color:#fff;height:60px;font-size:18px">
            <p>农兴论坛后台管理系统</p>
          </el-col>
          <el-col :span="4">
            <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" router background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">
              <el-submenu index="2">
                <template slot="title">控制台</template>
                <el-menu-item index="create">添加帖子</el-menu-item>
                <el-menu-item index="articles">帖子列表</el-menu-item>
                <!-- <el-menu-item index="2-3">选项3</el-menu-item> -->
              </el-submenu>
              <el-menu-item index="2" @click="logout" >退出</el-menu-item>

            </el-menu>

          </el-col>

        </el-row>

      </el-header>
      <el-container>
        <el-main>
          <router-view/>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeIndex:"2"
    }
  },methods:{
    logout(){
      this.deleteCookie('userInfo')
      this.$router.push({name:'login'})
    }
  }
}
</script>

<style scoped>
.el-header {
  padding: 0;
}
</style>



