import axios from 'axios'

let options = {}
// options.baseURL = `https://cnodejs.org/api/v1`
// options.baseURL = `nxforum`
// options.baseURL = `http://121.42.56.32:8080`
options.baseURL = `http://www.nxin.com.cn/forum/`
// options.baseURL = `http://localhost:81/`


let $axios

export default {
    install(vue) {
        if (!vue.$axios) {
            vue.$axios = axios.create(options)
        }

        vue.mixin({
            created: function () {
                this.$axios = vue.$axios
            }
        })
    }
}




