<template>
    <div>
        <el-row>
            <el-col :span="6" class="elblank"></el-col>
            <el-col :span="12">
                <transition name="form-fade" mode="in-out">
                    <section>
                        <div>
                            <p>
                                <strong>农兴论坛管理系统登陆</strong>
                            </p>
                        </div>
                        <el-form :model="loginForm" :rules="rules" ref="loginForm" @keydown.enter.native="submitForm('loginForm')">
                            <el-form-item prop="login_name">
                                <el-input v-model="loginForm.login_name" placeholder="用户名">
                                    <span></span>
                                </el-input>
                            </el-form-item>
                            <el-form-item prop="password">
                                <el-input type="password" placeholder="密码" v-model="loginForm.password"></el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="submitForm('loginForm')" class="submit_btn" :loading="isloading">登录</el-button>
                            </el-form-item>
                        </el-form>
                    </section>
                </transition>
            </el-col>
            <el-col :span="6" class="elblank"></el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    data() {
        return {
            isloading: false,
            loginForm: {
                login_name: 'nxforumadmin',
                password: '',
            },
            rules: {
                login_name: [
                    { required: true, message: '请输入用户名', trigger: 'blur' },
                ],
                password: [
                    { required: true, message: '请输入密码', trigger: 'blur' }
                ],
            },
        }
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate(async (valid) => {
                if (valid && this.loginForm.login_name == 'nxforumadmin') {
                    this.isloading = true
                    this.$axios.post('apiproxy.php', JSON.stringify({
                        type: 'mobapp/usr/login/v1',
                        data: {
                            login_name: this.loginForm.login_name,
                            password: this.loginForm.password
                        }
                    }))
                        .then(result => {
                            if (result.data.respnum == 1) {
                                this.setCookie('userInfo', encodeURIComponent(JSON.stringify(result.data.data[0])), 20)
                                this.isloading = false
                                if (this.isWeiXin()) {                                  
                                this.$router.push({ name: 'wearticles' })                                    
                                }else{
                                this.$router.push({ name: 'articlelist' })
                                }
                            }
                            else {
                                this.$message({
                                    type: 'error',
                                    message: res.data.respmsg
                                });
                                this.isloading = false
                            }
                        })
                }
                else {
                    this.$notify.error({
                        title: '错误',
                        message: '请输入正确的用户名密码',
                        offset: 100
                    });
                    this.isloading = false
                    return false
                }
            });
        },
        isWeiXin() {
            var ua = window.navigator.userAgent.toLowerCase();
            if (ua.match(/MicroMessenger/i) == 'micromessenger') {
                return true;
            } else {
                return false;
            }
        }
    }
}
</script>

<style lang="less" scoped>
.elblank {
    min-height: 20px;
}

.el-row {
    margin-top: 15%;
}
</style>




