import Vue from 'vue'
import Router from 'vue-router'
import { getCookie } from '@/util/cookie'
import Manager from '@/components/Manager'
import ArticleList from '@/views/ArticleList'
import WeArticles from '@/views/Articles'

import ArticleCreate from '@/views/ArticleCreate'
import Login from '@/views/Login'



Vue.use(Router)


 const router=new Router({
  routes: [
    {
      path: '/',
      name: 'manager',
      component: Manager,
      children:[
        {
          path: '/articles',
          name: 'articlelist',
          component: ArticleList
        },
        
        {
          path: '/create',
          name: 'articlecreate',
          component: ArticleCreate
        }
      ]
    },
    {
      path: '/wearticles',
      name: 'wearticles',
      component: WeArticles
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },

  ]
})

router.beforeEach((to, from, next) => {
  try {
    if (to.name!='login') {  // 判断该路由是否需要登录权限
      const userInfo = JSON.parse(decodeURIComponent(getCookie('userInfo')))
      if (userInfo.usr_guid) {  // 通过vuex state获取当前的token是否存在
        next();
      }
      else {
        next({
          path: '/login',
          query: { redirect: to.fullPath }  // 将跳转的路由path作为参数，登录成功后跳转到该路由
        })
      }
    }
    else {
      next();
    }
  }
  catch (e) {
    console.warn(e)
    next({
      path: '/login',
      query: { redirect: to.fullPath }  // 将跳转的路由path作为参数，登录成功后跳转到该路由
    })
  }
})



export default router


